$archive_entry_title_size: 14px;
$archive_entry_title_weight: 600;
$archive_entry_title_pt: 10px;

$entry_page_header_title_transform: uppercase;
$entry_page_header_title_size: 25px;
$entry_page_header_title_weight: 500;

.archive {
    .main-content{
        header {
            &.entry-page-header {
                &.entry-header {
                    .entry-title {
                        font-size: $entry_page_header_title_size;
                        font-weight: $entry_page_header_title_weight;
                        text-transform: $entry_page_header_title_transform;
                        margin-bottom: 10px;
                    }
                }
            }
        }
    }
}

.article-grid {
    .post-thumbnail {}
    .entry-content {
        header {
            &.entry-header {
                .entry-title {
                    font-size: $archive_entry_title_size;
                    font-weight: $archive_entry_title_weight;
                    padding-top: $archive_entry_title_pt;
                }
            }
        }
        
        p{
            a.read-more {}
        }
    }
}
.article-list {}